import React, { useEffect, useState } from "react";
import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import WizardMenu from "../../components/WisardMenu/WisardMenu";
import './css/index.css';
import useUser from "../../hooks/auth/useUser";
import {
    getAvailableCategoriesByEdition,
} from "../../api/categories";
import {
    getCampuses,
} from "../../api/campuses";
import Loading from "../../components/Loading/Loading";
import Dialog from "../../components/Dialog/Dialog";
import { register } from "../../api/auth";
import Alert from "../../components/PushMessage/Alert";
import { Navigate } from "react-router-dom";
import { checkIfPhoneExist, newEnrollment } from "../../api/enrollments";
import RegisterForm from "./Components/RegisterForm";
import UserDetailsForm from "./Components/UserDetailsForm";
import LoadingIcon from "../../components/Loading/Icon";
import BasicInformationForm from "./Components/BasicInformationForm";
import Campus from "./Components/Campus";
import Gift from "./Components/Gift";
import Checkout from "./Components/Checkout";
import { getGifts } from "../../api/gifts";

export default () => {
    const [categories, setCategories] = useState([]);
    const [userData, setUserData] = useUser();
    const [stateLoading, setStateLoading] = useState(false);
    const [action, setAction] = useState(false);
    const [stateAlert, setStateAlert] = useState(false);
    const [phone, setPhone] = useState('');
    const [playerTwo, setPlayerTwo] = useState(null);
    const [checkBox, setCheckBox] = useState(false);
    const [loadingInfo, setLoadingInfo] = useState(true)
    const [registered, setRegistered] = useState(false);
    const [campuses, setCampuses] = useState([]);
    const [favoritesCampuses, setFavoriteCampuses] = useState([]);
    const [gifts, setGifts] = useState([]);
    const [selectedGifts, setSelectedGifts] = useState([]);

    const checkUser = () => {
        if (isNaN(phone)) {
            setStateAlert({
                type: "war",
                message: "Compruebe que el número de teléfono sea válido",
            });
        } else {
            setLoadingInfo(true)
            setAction(true)
            checkIfPhoneExist(phone, userData.token, (json, error) => {
                if (json) {
                    if (json.code == "ok") {
                        setPlayerTwo(json.data)
                    }
                } else {
                    setStateAlert({
                        type: "war",
                        message: "Ha ocurrido un error en la verificación",
                    });
                }
                setLoadingInfo(false)
            })
        }
    }

    const handleEnrollmentSubmit = e => {
        e.preventDefault();

        const formValues = Object.fromEntries(new FormData(e.target).entries());
        const data = {
            categoryId: formValues.category,
            player2Id: playerTwo.id,
            favoritesCampuses: [...campuses],
            reserveTrack: formValues.reserveTrack,
            playOpeningDay: formValues.playOpeningDay,
            observations: formValues.observations,
            acceptDataProtectionPolicy: true,
            player1Gift: selectedGifts[0],
            player2Gift: selectedGifts[1],
            timesAvailabilities: [formValues.available_time1, formValues.available_time2]
        }

        setStateLoading(true)
        /*  newEnrollment(data, userData.token, (json, error) => {
              if (json) {
                  if (json.code == "ok") {
                      //Enrollment successfull
                      setStateLoading(false);
                      setStateAlert({
                          type: "ok",
                          message: json.message,
                      });
                  } else {
                      setStateAlert({
                          type: "war",
                          message: json.message,
                      });
                  }
              } else {
                  setStateLoading(false);
                  //Error enrollment
                  setStateAlert({
                      type: "err",
                      message: "Ocurrio un error!, " + error,
                  });
              }
  
          });
  
  
  
  */
        setTimeout(() => {
            setStateLoading(false);
            setStateAlert({
                type: "ok",
                message: 'Inscripcion creada satisfactoriamente',
            });
        }, 2000);

    }
    const info = () => {
        setStateLoading(true)
        getCampuses((json, error) => {
            if (json) {
                if (json.code == "ok") {
                    setCampuses([...json.data]);
                }
            }
        })
        getAvailableCategoriesByEdition((json, error) => {
            if (json) {
                if (json.code == "ok") {
                    setCategories([...json.data]);
                }
            }
            setStateLoading(false)
        });

        getGifts((json) => {
            if (json) {
                if (json.code == "ok") {
                    setGifts([...json.data]);
                }
            }
        })
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        const formValues = Object.fromEntries(new FormData(e.target).entries());
        const data = {
            name: formValues.name,
            lastName: formValues.lastName,
            password: formValues.password,
            email: formValues.email,
            phone: formValues.phone,
            referenceCode: userData.id,
            acceptPP: true
        };
        setStateLoading(true);
        register(data, (json, error) => {
            //Loading stop
            setStateLoading(false);
            if (json) {
                if (json.code == "ok") {
                    //Register successfull
                    setAction(false);
                    setRegistered(true)
                    setPlayerTwo(json.user)
                    setStateAlert({
                        type: "ok",
                        message: "Usuario registrado correctamente!",
                    });
                } else {
                    setStateAlert({
                        type: "war",
                        message: json.message,
                    });
                }
            } else {
                //Error register
                setStateAlert({
                    type: "err",
                    message: "Ocurrio un error!, " + error,
                });
            }

        });
    }

    useEffect(() => {
        info();
    }, []);

    useEffect(() => {
        console.log(selectedGifts);
    }, [selectedGifts]);

    useEffect(() => {
        setRegistered(false);
        setAction(false);
    }, [phone]);

    const handleRegister = () => {
        setRegistered(true);
    }

    return userData.permissions == "" ? <Navigate to={'/login'} replace /> :
        <>
            <NavBar />
            {stateLoading && <Loading />}
            {stateAlert && <Alert {...stateAlert} />}
            <div className="responsive-form">
                <form className="form" onSubmit={handleEnrollmentSubmit}>
                    <h1 className="text-4xl text-center text-blue-500 font-bold">Unirse al torneo <br /> <span><small></small></span></h1>
                    <div className="mt-8 relative flex justify-between">
                        <hr className="absolute w-full border-blue-500 top-1/2 -translate-y-1/2 translate-y-1/2" />
                        <div className="z-10 bg-blue-500 h-14 w-14 rounded-full shadow-md flex items-center justify-center cursor-pointer hover:scale-105 transition-transform duration-300 text-white">
                            <svg height="800px" width="800px" version="1.1" id="_x32_"
                                viewBox="0 0 512 512" className="h-1/2 w-1/2 text-white" fill="currentColor">
                                <g>
                                    <path class="st0" d="M290.671,135.434c37.324-3.263,64.949-36.175,61.663-73.498c-3.241-37.324-36.152-64.938-73.476-61.675
		                                        c-37.324,3.264-64.949,36.164-61.686,73.488C220.437,111.096,253.348,138.698,290.671,135.434z"/>
                                    <path class="st0" d="M311.31,406.354c-16.134,5.906-43.322,22.546-43.322,22.546s20.615-95.297,21.466-99.446
		                                        c8.71-41.829,33.463-100.86-0.069-136.747c-23.35-24.936-53.366-18.225-79.819,7.079c-17.467,16.696-26.729,27.372-42.908,45.322
		                                        c-6.55,7.273-9.032,14.065-5.93,24.717c3.332,11.515,16.8,17.226,28.705,12.871c16.134-5.895,43.3-22.534,43.3-22.534
		                                        s-12.595,57.997-18.869,87c-0.874,4.137-36.06,113.292-2.505,149.18c23.35,24.949,53.343,18.226,79.819-7.066
		                                        c17.467-16.698,26.729-27.373,42.908-45.334c6.55-7.263,9.009-14.054,5.93-24.706C336.66,407.733,323.215,402.01,311.31,406.354z"
                                    />
                                </g>
                            </svg>
                        </div>
                        <div className="z-10 bg-blue-500 h-14 w-14 rounded-full shadow-md flex items-center justify-center cursor-pointer hover:scale-105 transition-transform duration-300 text-white">
                            <svg fill="currentColor" className="text-white h-1/2 w-1/2" width="800px" height="800px" viewBox="0 0 50 50" version="1.2" baseProfile="tiny" xmlns="http://www.w3.org/2000/svg" overflow="inherit"><path d="M34.438 17.552c3.498 0 6.346-2.801 6.346-6.271 0-3.469-2.847-6.281-6.346-6.281-3.512 0-6.365 2.812-6.365 6.281 0 3.47 2.854 6.271 6.365 6.271zm14.562 15.305v-7.94l-8.565-4.817c-1.451-.728-3.064-.975-4.676-.635-.66.135-1.281.387-1.979.771-.076.021-2.619 1.13-4.097 4.541l-5.898 11.733s-5.811 1.113-8.243 1.577l-.595.351-1.505-5.381c-.623-2.188 1.701-5.36 1.788-5.489 1.538-2.225 1.959-5.156 1.151-8.059-.736-2.625-2.412-4.838-4.594-6.083-1.707-.959-3.626-1.223-5.368-.749-1.353.367-2.559 1.154-3.491 2.285l-.721 1.05c-1.254 2.183-1.543 4.919-.818 7.558.736 2.65 2.393 4.854 4.586 6.082l.525.275c.07.005 3.682 1.461 4.31 3.55-.008.011.948 3.427 1.728 6.211l-.594.27.116.077c-.176.269-.485.865-.463 1.475 0 .194.016.388.053.576.333 1.725 2.042 2.877 3.788 2.526l11.145-2.144s.948-.285 1.354-.571c.523-.367.917-1.137.917-1.137l2.929-5.823s2.019 9.41 2.058 9.584l-.465.479h15.203l-.381-2.203c0-.022-.479-2.111-.479-2.111.016.015-1.304-5.862-2.313-10.462 1.608.92 3.065 1.738 3.146 1.782l.448.851zm-38.313-4.826c-1.129.3-2.335.118-3.491-.519-1.61-.9-2.854-2.575-3.387-4.596-.568-2.014-.361-4.077.556-5.667l.512-.738c.604-.733 1.336-1.223 2.214-1.466 2.804-.753 5.892 1.541 6.89 5.124.983 3.567-.489 7.107-3.294 7.862z" />
                            </svg>
                        </div>
                        <div className="z-10 bg-blue-500 h-14 w-14 rounded-full shadow-md flex items-center justify-center cursor-pointer hover:scale-105 transition-transform duration-300 text-white">
                            <svg width="800px" height="800px" viewBox="0 0 24 24" fill="currentColor" className="h-1/2 w-1/2 text-white">
                                <path d="M19 10.0802V20.0802H5V10.0802M19 10.0802H5M19 10.0802H20V7.0802H4V10.0802H5M12 7.0802C12.8333 5.24687 14.9999 1.5802 16.9999 3.5802C18.9999 5.5802 14.5 6.91353 12 7.0802ZM12 7.0802C11.1667 5.24687 8.99999 1.5802 6.99999 3.5802C4.99999 5.5802 9.5 6.91353 12 7.0802Z" stroke="#000000" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" />
                            </svg>
                        </div>
                        <div className="z-10 bg-blue-500 h-14 w-14 rounded-full shadow-md flex items-center justify-center cursor-pointer hover:scale-105 transition-transform duration-300 text-white">
                            <svg width="800px" height="800px" viewBox="0 0 32 32" version="1.1" className="h-1/2 w-1/2 text-white" fill="currentColor">
                                <title>credit-card</title>
                                <path d="M0 26.016q0 0.832 0.576 1.408t1.44 0.576h28q0.8 0 1.408-0.576t0.576-1.408v-20q0-0.832-0.576-1.408t-1.408-0.608h-28q-0.832 0-1.44 0.608t-0.576 1.408v20zM2.016 26.016v-14.016h28v14.016h-28zM2.016 8v-1.984h28v1.984h-28zM4 24h4v-1.984h-4v1.984zM4 20h6.016v-5.984h-6.016v5.984zM10.016 24h4v-1.984h-4v1.984zM16 24h4v-1.984h-4v1.984zM22.016 24h1.984v-1.984h-1.984v1.984zM26.016 24h1.984v-1.984h-1.984v1.984z"></path>
                            </svg>
                        </div>
                    </div>
                    <WizardMenu
                        checkUser={checkUser}
                        registered={registered}
                        sections={[
                            <BasicInformationForm phone={phone} setPhone={setPhone} categories={categories} />
                            ,
                            <Campus favoritesCampuses={favoritesCampuses} setFavoriteCampuses={setFavoriteCampuses} campuses={campuses} />
                            ,
                            <Gift selectedGifts={selectedGifts} setSelectedGifts={setSelectedGifts} gifts={gifts} />
                            ,
                            <Checkout />
                            ,
                        ]}
                    />
                </form>
            </div>
            <Footer />
            <Dialog action={action && !registered} setAction={setAction} >
                {
                    loadingInfo ?
                        <div className="flex items-center justify-start"><LoadingIcon width={25} height={25} /><span className="ml-2">Verificando datos...</span></div> :
                        !playerTwo ?
                            <RegisterForm userData={userData} handleSubmit={handleSubmit} phone={phone} /> : <UserDetailsForm playerTwo={playerTwo}
                                handleClick={handleRegister} />
                }
            </Dialog>
        </>


}