import React, { useEffect } from "react";
import CheckboxCard from "./CheckboxCard";
import EmptySection from "../../../components/EmptySection";

const Gift = ({ selectedGifts, setSelectedGifts, gifts }) => {
    
    return <div className="grid-gallery">
        {
            gifts && gifts.length ? gifts.map((el) =>
                <CheckboxCard src={el.photo} key={el.id} id={el.id} title={el.name} subtitle={`${el.price} $`} data={selectedGifts} setData={setSelectedGifts} limit={2} />)
                : <EmptySection text={'No hay sedes disponibles actualmente'} />
        }
    </div>
}

export default Gift;